@import url("https://fonts.googleapis.com/css?family=Lobster");
* {
  margin: 0;
  padding: 0;
}
.title {
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  font-family: "Lobster", "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: 80px;
  height: 150px;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 20px;
  background-color: rgb(52, 58, 64);
}
.icon {
  margin: 20px;
}
nav {
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
  height: 60px;
}

.nav-bar {
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  -ms-flex-pack: center;
      justify-content: center;
}

nav > ul > li > .active {
  font-weight: bold;
  text-decoration: underline;
  color: rgb(52, 58, 64);
}

.nav-item {
  color: gray;
  display: -ms-flexbox;
  display: flex;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: 15px;
  margin-top: 10px;
  padding: 10px 10px;
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
  -ms-flex-pack: center;
      justify-content: center;
}

.nav-input {
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
  -webkit-margin-start: 200px;
          margin-inline-start: 200px;
}

.nav-input:hover {
  text-decoration: none;
}

.nav-item:hover {
  font-size: 19px;
  text-decoration: underline;
  color: gray;
}
.card {
  -webkit-box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.2);
  width: 300px;
  margin: 50px auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: rgba(0, 0, 0, 0.2);
}

.card-image {
  display: block;
  width: 100%;
  height: 15rem;
  background-position: center center;
  -o-object-fit: cover;
     object-fit: cover;
}

.card-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
}
.card-content > p {
  padding: 10px;
}

.card-button {
  padding: 10px;
  text-align: center;
}
.scroll-to {
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.container {
  display: grid;
  grid-template-columns: 400px 400px 400px;
  -ms-flex-pack: center;
      justify-content: center;
}
.load-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.load {
  position: absolute;
  top: 50%;
  font-size: 100px;
  color: gray;
}

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

.rotating {
  -webkit-animation: rotating 2s linear infinite;
          animation: rotating 2s linear infinite;
}



